import GeneratedObject from "./GeneratedObject"
import { consts } from "./GenerationConstants"
import Generator from "./Generator"
import Measurement from "./Measurement"
import Supercluster from "./Supercluster"
/*
All properties or dimensions are lazy generated.
This means that everything is accessed via functions look for the field, 
if it doesn't exist generate it from a freshly seeded random generator.
The seed will always be the same therefore the generated dimension will always be the same.
*/


export default class Universe extends GeneratedObject {

    
    private _superclusterCount?: number
    private _superclusterNames?: string[]
    private _superclusters!: Supercluster[]
    
    private _galaxyCount?: number
    private _approximateStarCount?: number

    constructor(name: string){
        super(name, 'universe', null)
    }

    private _radius?: Measurement
    Radius(){
        return this._radius ||
        (this._radius = 
            this.GenerateMeasurement("Radius", consts.universe.radius.min, consts.universe.radius.max, "ly"))
    }

    private _density?: Measurement
    Density(){
        return this._density ||
        (this._density = 
            this.GenerateFloatMeasurement("Density", consts.universe.density.min, consts.universe.density.max, "percent"))
    }

    // Super clusters
    SuperclusterCount(){
        return this._superclusterCount ||
        (this._superclusterCount =
            Math.floor(this.Radius().number * this.Density().number / 100000000))
    }

    SuperclusterNames(){
        return this._superclusterNames || 
        (this._superclusterNames = [...Array(this.SuperclusterCount())].map((_, i) => Generator.generateSeededName(this.Seed(":SuperclusterName:" + i))))
    }

    Superclusters(){
        return this._superclusters ||
        (this._superclusters = 
            [...Array(this.SuperclusterCount())].map((_, i) => new Supercluster(this.SuperclusterNames()[i], this.locationIdentifier)))
    }

    // Counts
    EstimatedGalaxyCount(){
        return Math.round(
            this.SuperclusterCount() *
            (consts.supercluster.radius.avg * consts.supercluster.density.avg / 100000) *
            consts.supercluster.galaxyGroupsPerMillionLightYears.avg *
            (consts.galaxyGroup.radius.avg / 1000000) *
            consts.galaxyGroup.galaxysPerMillionLightYears.avg *
            this.Density().number)
    }

    EstimatedStarCount(){
        return Math.round(
            this.EstimatedGalaxyCount() *
            consts.galaxy.stars.avg *
            this.Density().number)
    }
}
