import { significantUnits } from "./BigNumber";

const unitDefinitions = [
    // Length
    { abbreviation: "km", name: "kilometre" },
    { abbreviation: "au", name: "astronomical unit" },
    { abbreviation: "pc", name: "parsec" },
    { abbreviation: "mi", name: "mile" },
    { abbreviation: "ly", name: "light year" },
    { abbreviation: "m", name: "metre" },
    { abbreviation: "R⊙", name: "solar radius" },
    // Mass
    { abbreviation: "g", name: "gram" },
    { abbreviation: "kg", name: "kilogram" },
    { abbreviation: "M⊕", name: "earth mass" }, // = 5.9722×10^24 kg
    { abbreviation: "MJup", name: "jupiter mass" }, // = 1.898×10^27 kg
    { abbreviation: "M⊙", name: "solar mass" }, // = 1.98855×10^30 kg

    // Time
    { abbreviation: "s", name: "second" },
    { abbreviation: "min", name: "minute" },
    { abbreviation: "h", name: "hour" },
    { abbreviation: "s", name: "second" },
    // Calender
    { abbreviation: "y", name: "year" },
    { abbreviation: "100 y", name: "century" },
    { abbreviation: "1000 y", name: "millenia" },
    // Electric Current
    { abbreviation: "A", name: "ampere" },
    // Temperature
    { abbreviation: "K", name: "kelvin" },
    { abbreviation: "℃", name: "celcius" },
    { abbreviation: "℉", name: "fahrenheit" },
    // Amount of substance
    { abbreviation: "mol", name: "mole" },
    // Luminous intensity
    { abbreviation: "cd", name: "candela" },
    // Luminous flux
    { abbreviation: "lm", name: "lumen" },
    // Bolometric luminosity
    { abbreviation: "L⊙", name: "solar luminosity" },
    // Pressure 
    // Pressure is generally measured in units of Pascals. Atmospheric scientists, however, like to express pressure in units of bars. For the record, 1 Pascal = 1 Newton per square meter, and 1 bar = 100,000 Pascals. The average atmospheric pressure at the surface of the Earth is approximately 1 bar. That is why atmospheric scientists like to express pressure in units of bars. If someone told you that the atmospheric pressure at the surface of Venus was 90 bars, then you would know that it was about 90 times greater than that of Earth.
    // Angle
    { abbreviation: "radians", name: "radians" },
    { abbreviation: "degrees", name: "degrees" },
    { abbreviation: "minutes", name: "minutes" },
    { abbreviation: "seconds", name: "seconds" },
]

export default class Measurement{
    number: number;
    unit: string;
    
    constructor (number: number, unit: string) {
        this.number = number;
        this.unit = unit;
    }

    unitFull() {
        return unitDefinitions.find(ud => ud.abbreviation === this.unit || ud.name === this.unit)?.name || this.unit
    }

    unitAbbr() {
        return unitDefinitions.find(ud => ud.abbreviation === this.unit || ud.name === this.unit)?.abbreviation || this.unit
    }
    ToString() {
        return `${significantUnits(this.number)} ${this.unitFull()}s`
    }
    numberAbbr() {
        return significantUnits(this.number)
    }
}

/* Problem

Converting between units or even just performing unit operations (eg. 2cm x 3cm) should be a solveable problem.

This is partly helped by the measurement class which allows you to attach a qualifier to a unit of measurement, now
instead of having long unweildy variable names, you can keep them short and to the point and harness the power of the
measurements libraries capabilities.
instead of:
var houseLengthInMetres = 50;
You can specify:
var houseLength = new Measurement(50, "m");

Units

A unit of measurement can be simple (eg. metres) or complex (eg. g/cm^3)

*/
