export const significantUnits = function(number: number) {
    const log10ofnumber = Math.floor(Math.log10(number));
    if (log10ofnumber < 6) {
        return Math.round(number) + "";
    }
    // if (log10ofnumber >= 3 && log10ofnumber < 6) {
    //     return Math.round(number / 10) / 100 + " thousand";
    // }
    if (log10ofnumber >= 6 && log10ofnumber < 9) {
        return Math.round(number / 10000) / 100 + " million";
    }
    if (log10ofnumber >= 9 && log10ofnumber < 12) {
        return Math.round(number / 10000000) / 100 + " billion";
    }
    if (log10ofnumber >= 12 && log10ofnumber < 15) {
        return Math.round(number / 10000000000) / 100 + " trillion";
    }
    if (log10ofnumber >= 15 && log10ofnumber < 18) {
        return Math.round(number / 10000000000000) / 100 + " quadrillion";
    }
    if (log10ofnumber >= 18) {
        return Math.round(number / 10000000000000000) / 100 + " quintillion"
    }
     // 9 quadrillion is approxiamtely the largest number js can handle natively.
}
