import GalaxyGroup from "./GalaxyGroup"
import GeneratedObject from "./GeneratedObject"
import { consts } from "./GenerationConstants"
import Generator from "./Generator"
import ICompareable from "./IComparable"
import LocationIdentifier from "./LocationIdentifier"
import Measurement from "./Measurement"
import Universe from "./Universe"
/*
All properties or dimensions are lazy generated.
This means that everything is accessed via functions look for the field, 
if it doesn't exist generate it from a freshly seeded random generator.
The seed will always be the same therefore the generated dimension will always be the same.
*/

export default class Supercluster extends GeneratedObject implements ICompareable {

    constructor(name: string, locationIdentifier: LocationIdentifier){
        super(name, 'supercluster', locationIdentifier)
    }

    // IComparable functions
    compareFunctions = [
        { field: 'name', compare: (a: Supercluster, b: Supercluster, descending: boolean) => descending ? a.name.localeCompare(b.name) : b.name.localeCompare(a.name) },
        { field: 'radius', compare: (a: Supercluster, b: Supercluster, descending: boolean) => descending ? a.Radius().number - b.Radius().number : b.Radius().number - a.Radius().number },
        { field: 'density', compare: (a: Supercluster, b: Supercluster, descending: boolean) => descending ?  a.Density().number - b.Density().number : b.Density().number - a.Density().number },
        { field: 'distance', compare: (a: Supercluster, b: Supercluster, descending: boolean) => descending ?  a.DistanceFromCenter().number - b.DistanceFromCenter().number : b.DistanceFromCenter().number - a.DistanceFromCenter().number },
        { field: 'galaxyGroupCount', compare: (a: Supercluster, b: Supercluster, descending: boolean) => descending ?  a.GalaxyGroupCount() - b.GalaxyGroupCount() : b.GalaxyGroupCount() - a.GalaxyGroupCount() },
        { field: 'estimatedGalaxyCount', compare: (a: Supercluster, b: Supercluster, descending: boolean) => descending ?  a.EstimatedGalaxyCount() - b.EstimatedGalaxyCount() : b.EstimatedGalaxyCount() - a.EstimatedGalaxyCount() },
        { field: 'stars', compare: (a: Supercluster, b: Supercluster, descending: boolean) => descending ?  a.EstimatedStarCount() - b.EstimatedStarCount() : b.EstimatedStarCount() - a.EstimatedStarCount() },
        { field: 'colour', compare: (a: Supercluster, b: Supercluster, descending: boolean) => descending ?  a.Colour().localeCompare(b.Colour()) : b.Colour().localeCompare(a.Colour()) }
    ]
    comparableFields: string[] = this.compareFunctions.map(cf => cf.field)

    compare(compareTo: ICompareable, compareBy: string[], compareDesc: boolean[]): number {
        return this.compareFunctions.find(cf => cf.field === compareBy[0])?.compare(this, compareTo as Supercluster, compareDesc[0]) || 0
    }

    // Universe
    Universe() {
        return new Universe(this.locationIdentifier.locationNames.universe)
    }

    private _radius?: Measurement
    Radius(){
        return this._radius ||
        (this._radius = 
            this.GenerateFloatMeasurement("Radius", consts.supercluster.radius.min, consts.supercluster.radius.max, "ly"))
    }

    private _density?: Measurement
    Density(){
        return this._density ||
        (this._density = 
            this.GenerateFloatMeasurement("Density", consts.supercluster.density.min, consts.supercluster.density.max, "percent"))
    }

    private _colour?: string
    Colour(){
        return this._colour ||
        (this._colour = this.GenerateColour("Colour"))
    }

    private _distanceFromCenter?: Measurement
    DistanceFromCenter() {
        return this._distanceFromCenter ||
        (this._distanceFromCenter = this.GenerateFloatMeasurement("DistanceFromCenter", 0, this.Universe().Radius().number, "ly"))
    }
    
    private _longitude?: Measurement
    Logitude() {
        return this._longitude ||
        (this._longitude = this.GenerateFloatMeasurement("Logitude", 0, 359, "degrees"))
    }
    
    private _latitude?: Measurement
    Latitude() {
        return this._latitude ||
        (this._latitude = this.GenerateFloatMeasurement("Latitude", 0, 359, "degrees"))
    }
    
    private _galaxyGroupCount?: number
    GalaxyGroupCount(){
        return this._galaxyGroupCount ||
        (this._galaxyGroupCount =
            // Math.floor(this.Density().number * this.GenerateInt("GalaxyGroupCount", consts.supercluster.galaxyGroupsPerMillionLightYears.min, consts.supercluster.galaxyGroupsPerMillionLightYears.max)))
            Math.floor(this.Radius().number / 1000000 * consts.supercluster.maximumGalaxyGroupsPerMillionLightYears * this.Density().number))
    }

    private _galaxyGroupNames?: string[]
    GalaxyGroupNames(){
        return this._galaxyGroupNames || 
        (this._galaxyGroupNames = [...Array(this.GalaxyGroupCount())].map((_, i) => Generator.generateSeededName(this.Seed("GalaxyGroupName:" + i))))
    }

    private _galaxyGroups?: GalaxyGroup[]
    GalaxyGroups() {
        return this._galaxyGroups ||
        (this._galaxyGroups = 
            [...Array(this.GalaxyGroupCount())].map((_, i) => new GalaxyGroup(this.GalaxyGroupNames()[i], 'galaxygroup', this.locationIdentifier)))
    }

    EstimatedGalaxyCount(){
        return Math.round(
            (this.GalaxyGroupCount() *
            (consts.galaxyGroup.radius.avg) *
            consts.galaxyGroup.galaxysPerMillionLightYears.avg / 1000000 *
            this.Density().number))
    }

    private _galaxyCount?: number
    GalaxyCount() {
        return this._galaxyCount ||
        (this._galaxyCount = 
            this.GalaxyGroups().reduce((total, gg) => total + gg.GalaxyCount(), 0))
    }

    EstimatedStarCount(){
        return Math.round(
            this.EstimatedGalaxyCount() *
            consts.galaxy.stars.avg *
            this.Universe().Density().number)
    }

}