import Generator from "./Generator";
import LocationIdentifier from "./LocationIdentifier";
import Measurement from "./Measurement";

class Breadcrumb{
    text: string
    href: string
    query?: {[key: string]: string}
    constructor(text: string, href: string, query: {[key: string]: string} | null) {
        this.text = text
        this.href = href
        if (query) {
            this.query = query
        }
    }    
}

export default abstract class GeneratedObject {

    name: string
    locationIdentifier: LocationIdentifier

    constructor(name: string, type: string, locationIdentifier: LocationIdentifier | null) {
        this.name = name
        this.locationIdentifier = new LocationIdentifier(locationIdentifier, type, name)
    }

    getBreadcrumbs() : Breadcrumb[] {
        return [new Breadcrumb('Multiverse', '/', null)]
            .concat(Object.keys(this.locationIdentifier.locationNames)
            .map(locationKey => 
                new Breadcrumb(
                    `${locationKey[0].toUpperCase() + locationKey.substr(1)}: ${this.locationIdentifier.locationNames[locationKey]}`,
                    `/${locationKey}/`,
                    this.LocationParams()
                )
            )
        )
    }

    BaseSeed(){
        return Object.values(this.locationIdentifier.locationNames)
            .reduce((seed, name) => `${seed}:${name}`, '')
    }

    Seed(subseed?: string) {
        return subseed ? `${this.BaseSeed()}:${subseed}` : this.BaseSeed()
    }

    LocationParams() {
        return this.locationIdentifier?.locationNames
    }
    
    GenerateMeasurement(seedExtension: string, min: number, max: number, type:string) {
        return new Measurement(Generator.randSeededInt(this.Seed(seedExtension), min, max), type)
    }
    
    GenerateFloatMeasurement(seedExtension: string, min: number, max: number, type:string) {
        return new Measurement(Generator.randSeededFloat(this.Seed(seedExtension), min, max), type)
    }
    
    GenerateInt(seedExtension: string, min: number, max: number) {
        return Generator.randSeededInt(this.Seed(seedExtension), min, max)
    }

    GenerateColour(seedExtension: string): string {
        return Generator.color(this.Seed(seedExtension))
    }

}