
const locationHeirachy = [
    'universe',
    'supercluster',
    'galactic group',
    'galaxy',
    'galactic sector',
    'galactic sub-sector',
    'star',
    'planet'
]

export default class LocationIdentifier {

    locationNames: {[key: string]: string}

    constructor(parentLocationIdentifier: LocationIdentifier | null, locationType: string, name: string){
        this.locationNames = {}
        if(parentLocationIdentifier){
            Object.assign(this.locationNames, parentLocationIdentifier.locationNames)
        }
        this.locationNames[locationType] = name
    }

    generateTo(locationType: string){
        const query = {} as {[key: string]: string}
        for (   let index = 0;
                index < locationHeirachy.length && locationHeirachy[index-1] !== locationType;
                index++) {
            query[locationHeirachy[index]] = this.locationNames[locationHeirachy[index]]
        }
        return {
            path: `/${locationType}/`,
            query: query
        }
    }

    generateBreadCrumbs(){
        return [{ description: 'Multiverse', to: { path: '/' }}].concat(
            Object.keys(this.locationNames).map(key => {
                return { 
                    description: `${key[0].toUpperCase()}${key.substr(1)}:${this.locationNames[key]}`, 
                    to: this.generateTo(key)
                }
            })
        )
    }
}