const Chance = require('chance');

export default class Generator {

    static randInt(chance: typeof Chance, min: number, max: number) {
        return chance.integer({ min: min, max: max });
    }
    
    static randSeededInt(seed: string, min: number, max: number) {
        return new Chance(this.hashToInt(seed)).integer({ min: min, max: max }) as number
    }

    static randFloat(chance: typeof Chance, min: number, max: number) {
        return Math.abs(chance.floating({ min: min, max: max }));
    }

    static randSeededFloat(seed: string, min: number, max: number) {
        return Math.abs(new Chance(this.hashToInt(seed)).floating({ min: min, max: max }));
    }

    static color(seed: string) {
        return new Chance(this.hashToInt(seed)).color({format: 'hex'});
    }

    static hashToInt(s: string) { return this.djb2Code(s) }
    // http://erlycoder.com/49/javascript-hash-functions-to-convert-string-into-integer-hash-
    static djb2Code(str: string) {
        let hash = 5381;
        for (let i = 0; i < str.length; i++) {
            hash = ((hash << 5) + hash) + str.charCodeAt(i) /* hash * 33 + c */
        }
        return hash;
    }

    static generateName(chance: typeof Chance) {
        return chance.pickone([
            chance.capitalize(chance.word({ syllables: 1 })) + "-" + chance.natural({ min: 1, max: 999 }),
            chance.capitalize(chance.word({ syllables: 2 })),
            chance.capitalize(chance.word({ syllables: 2 })) + "-" + chance.natural({ min: 1, max: 999 }),
            chance.capitalize(chance.word({ syllables: 3 })),
        ]) as string
    }

    static generateSeededName(seed: string) {
        return this.generateName(new Chance(this.hashToInt(seed)));
    }

    static generateUnseededName() {
        return this.generateName(new Chance())
    }
}