/*
All properties or dimensions are lazy generated.
This means that everything is accessed via functions look for the field, 
if it doesn't exist generate it from a freshly seeded random generator.
The seed will always be the same therefore the generated dimension will always be the same.
*/

import GeneratedObject from "./GeneratedObject"
import Generator from "./Generator";
import Measurement from "./Measurement";

const minGalaxyGroupRadius = 5000000
const maxGalaxyGroupRadius = 50000000
const minGalaxysPerMillionLightYears = 1;
const maxGalaxysPerMillionLightYears = 5;

const minStarsPerGalaxy = 50000000
const maxStarsPerGalaxy = 100000000000

export default class GalaxyGroup extends GeneratedObject {

    private _radius?: Measurement
    private _galaxyCount?: number;
    private _approximateStarCount?: number;
    
    // Radius
    Radius(){
        return this._radius ||
        (this._radius = 
            this.GenerateMeasurement("Radius", minGalaxyGroupRadius, maxGalaxyGroupRadius, "ly"))
    }

    GalaxyCount(){
        return this._galaxyCount ||
        (this._galaxyCount =
            Math.floor(this.Radius().number / 1000000 / this.GenerateInt("GalaxyCount", minGalaxysPerMillionLightYears, maxGalaxysPerMillionLightYears)))
    }

    ApproximateStarCount(){
        return this._approximateStarCount ||
        (this._approximateStarCount = 0)
    }

}

